<template>

  <div id="ongoing">
    <Navbar/><br>
    <vs-row vs-justify="center" v-bind:key="index" v-for="row, index in 3">
      <vs-col vs-justify="center" vs-align="center" vs-w="10">
        <br><br>
        <vs-card>
          <div slot="header">
            <h3>
              진행 중인 활동
            </h3>
          </div>
              <h1 style="color:#1F74FF;">회의</h1>
              <div>2020년 3월 2일</div>
              <h2>13:00 ~</h2>

              <vs-divider>작성자</vs-divider>
              <vs-row vs-justify="center">
                <vs-chip transparent color="primary">가나다</vs-chip>
              </vs-row>
              <vs-divider>참여자</vs-divider>
              <vs-row vs-justify="center">
                <vs-chip transparent>가나다</vs-chip>
                <vs-chip transparent>라마바</vs-chip>
                <vs-chip transparent>아자차</vs-chip>
              </vs-row>
          <div slot="footer">
            <vs-row vs-justify="center">
              <vs-button type="gradient" color="primary" icon="done">
                종료하기</vs-button>
            </vs-row>
          </div>
        </vs-card>
      </vs-col>
    </vs-row>
    <br<br>
    <vs-row vs-justify="center">
    <vs-button color="warning" type="filled" icon="navigate_next" icon-after="true" to="/register">
      활동 등록하기
    </vs-button></vs-row>
    <br><br>
  </div>
<!-- list로 만들어보기? -->
</template>

<script>
import Navbar from './Navbar'

export default {
  data () {
    return {
      active:false
    }
  },
  components:{ Navbar }
}
</script>

<style>
</style>



