<template>

  <div id="register">
    <Navbar/><br><br>

    <vs-list>
      <vs-list-header icon="assignment" title="활동 내용"></vs-list-header>
      <vs-list-item>
        <vs-col :key="index" v-for="(content, index) in contents"
                vs-type="flex" vs-justify="space-between" vs-w="5" vs-offset="0.8">
          <vs-radio v-model="selected_content" :vs-value="content" >{{ content }}</vs-radio>
        </vs-col>
      </vs-list-item>
      <vs-list-header icon="group" title="참여자"></vs-list-header>

      <vs-list-item>
        <vs-col :key="index" v-for="(name, index) in names" vs-justify="center" vs-w="4">
          <vs-checkbox v-model="selected_names" :vs-value="name" >{{ name }}</vs-checkbox>
        </vs-col>
      </vs-list-item>
      <vs-list-header icon="create" title="등록자"></vs-list-header>
      <vs-list-item>
        <vs-col :key="index" v-for="(name, index) in names" vs-justify="center" vs-w="4">
          <vs-radio v-model="creator" :vs-value="name" >{{ name }}</vs-radio>
        </vs-col>
      </vs-list-item>

      <vs-list-header icon="create" title="활동 시간"></vs-list-header>
        <span> 진행 여부?? </span>
        <vs-switch v-model="isOngoing">
          <span slot="on">On&nbsp</span>
          <span slot="off">&nbspOff</span>
        </vs-switch>
      <vs-list-item>
        <vs-row vs-justify="center">
          <vs-row vs-w="4.5">
            <vs-input v-model="date" label-placeholder="날짜" v-on:click="opendatePicker"></vs-input>
          </vs-row>
        </vs-row>
        <div style="line-height:0.7em"><br></div>
        <vs-row vs-align="center" vs-justify="center">
          <vs-row vs-w="3">
            <vs-input v-model="s_time" label-placeholder="시작 시각" v-on:click="openstimePicker"></vs-input>
          </vs-row>
            <span>&nbsp&nbsp&nbsp</span>
          <vs-row vs-w="3">
            <vs-input v-model="e_time" label-placeholder="종료 시각" v-on:click="openetimePicker"></vs-input>
          </vs-row>
        </vs-row><br>
      </vs-list-item>
    </vs-list><br>

    <vs-row vs-justify="center">
      <vs-button color="warning" icon="done" vs-w="4">등록하기</vs-button>
    </vs-row>
    <datetime hidden type="date" ref="datePicker" v-model="date"></datetime>
    <datetime hidden type="time" ref="stimePicker" v-model="s_time" v-bind:minute-step="5" use12-hour></datetime>
    <datetime hidden type="time" ref="etimePicker" v-model="e_time" v-bind:minute-step="5" use12-hour></datetime>
  </div>
</template>

<script>
import Navbar from './Navbar'

export default {
  data () {
    return {
      selected_content: "",
      contents: ["회의", "창고 개방", "공지문 작성", "설문지 제작", "예결산 작성"],
      selected_names: [],
      creator: "",
      names: [ "가가가", "나나나", "다다다", "라라라", "마마마", "바바바", "아아아"],
      datetime: {
        date:"",
        s_time: "",
        e_time: ""
      },
      isOngoing: true
    }
  },
  methods: {
    opendatePicker : function(){
      this.$refs.datePicker.open(event)
      console.log(this.$refs.datePicker.inputValue)
    },
    openstimePicker : function(){
      this.$refs.stimePicker.open(event)
      console.log(this.$refs.stimePicker.inputValue)
    },
    openetimePicker : function(){
      this.$refs.etimePicker.open(event)
      console.log(this.$refs.edatePicker.inputValue)
    }
  },
  computed:{
    date: {
      get() { return this.datetime.date },
      set() { this.datetime.date = this.$refs.datePicker.inputValue }
    },
    s_time: {
      get() { return this.datetime.s_time },
      set() { this.datetime.s_time = this.$refs.stimePicker.inputValue}
    },
    e_time: {
      get() { return this.datetime.e_time },
      set() { this.datetime.e_time = this.$refs.etimePicker.inputValue}
    },

  },
  components:{ Navbar }
}
</script>

<style>

</style>



