<template>
  <div class="top" style="z-index:2;">
    <vs-navbar collapse v-model="activeItem" class="nabarx">
      <div slot="title">
        <vs-navbar-title>
          Hello world
        </vs-navbar-title>
      </div>
      <hr style="border: solid 1px #D8D8D8; background: #D8D8D8;">
      <vs-navbar-item index="0">
        <a href="/ongoing">진행 중인 활동</a>
      </vs-navbar-item>
      <hr class="divider">
      <vs-navbar-item index="1">
        <a href="/completed">완료된 활동</a>
      </vs-navbar-item>
      <hr class="divider">
      <vs-navbar-item index="2">
        <a href="#">Update</a>
      </vs-navbar-item>
    </vs-navbar>
  </div>
</template>
<script>
export default {
  data () {
    return {
      activeItem: 0
    }
  }
}
</script>

<style>
  .top{
    position: fixed;
    width: 100%;
    background: #FFFFFF;
  }
  .divider{
    width: 90%;
    margin-left: 5%;
    border: solid 0.7px #D8D8D8;
  }
</style>



