<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Enter the code</h2>
    <input
      v-model="inputcode"
      v-on:keyup.enter="checkCode"
      type="password"/>
    <button v-on:click="checkCode">OK</button>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Activity Credit Certificate',
      inputcode: "",
      code: "dorm2020!"
    }
  },
  methods: {
    checkCode: function() {
      if(this.inputcode == this.code) {
        this.$router.push("/ongoing"); // Login 페이지로 보내줌
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
a {
  color: #42b983;
}
</style>



